.header {
  display: flex;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 20px;
  width: 100%;
  height: 100px;
}

.header-link {
  font-weight: 900;
  line-height: 100%;
  margin-right: auto;
}

.header-link:hover {
  transform: scale(1.2);
  transition: transform .3s  ease;
}

@media screen and (max-width: 1440px) {
  .header-link {
    font-size: 22px;
  }
}

@media screen and (max-width: 1180px) {
  .header-link {
    font-size: 20px;
  }
}

@media screen and (max-width: 1023px) {
  .header {
    align-items: center;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    height: 150px;
  }
  .header-link {
    font-size: 32px;
    margin-right: auto;
  }
}

@media screen and (max-width: 768px) {
  .header-link {
    font-size: 18px;
    margin-right: 50px;
  }
}

@media screen and (max-width: 610px) {

  .header-link {
    font-size: 20px;
    margin-right: auto;
    padding: 0;
  }
  .header-link:hover {
    transform: scale(0.9);
    transition: transform .3s  ease;
  }
}
